<script>


  import axios from 'axios'
  export default {
    data() {

      const validatePhone = (rule, value, callback) => {
        if (!value) {
          return callback(new Error('号码不能为空!'))
        }
        setTimeout(() => {
          if (!phoneReg.test(value)) {
            callback(new Error('请输入有效的手机号!'))
          } else {
            this.form.phone=value
            const url = `http://localhost/buyerAccountTelExise?uTel=${this.form.phone}`
            axios.get(url).then(response => {
              if(response.data.msg=='canRegister'){

                callback()
              }
              console.log(response.data)
              callback(new Error('手机号已经注册!'))
            })
          }
        }, 10)
      }
      const validatePass0=(rule,value,callback)=>{
          if (!value) {
            callback(new Error('请输入验证码'))
          } else {
            const url = `http://localhost/validateSmsVCode?vcode=${value}`
            axios.get(url).then(response => {
              console.log(response.data)
              if (response.data.msg == 'vcodeCorrect') {
                callback()
              }
              callback(new Error('验证码输入有误'))
            })

          }
        }
      return {
        time: 0,
        emailTime:0,
        btntxt:"获取验证码",
        btnname:"获取验证码",
        disabled:false,
        isdisabled:false,
        activeName2: 'first',
        form: {
          phone:'',
          input1:'',
          email:'',
          input2:''
        },
        // 校验规则
        rules: {
          // 校验手机号码，主要通过validator来指定验证器名称
          phone: [{ validator: validatePhone, trigger: 'blur' }],
          input1:[
            {
              validator: validatePass0, trigger: 'blur'
            }
          ],
          email: [{ validator: validatePhone, trigger: 'blur' }],
          input2:[
            {
              validator: validatePass0, trigger: 'blur'
            }
          ],
        }
      }
    },
    methods: {
      handleClick(tab, event) {
        console.log(tab, event);
      },
      sendMsg(){
        this.time=60
        this.disabled=true
        this.timer()
        axios.get(`http://localhost/buyerSendSmsVcode?phoneNum=${this.form.phone}`,{
          xhrFields :
            {
              withCredentials: true
            }
        }).then(

          res=>{

            console.log(res.data)
          })

      },
      timer() {
        if (this.time > 0) {
          this.time--;
          this.btntxt=this.time+"s后重新获取";
          setTimeout(this.timer, 1000);
        } else{
          this.time=0;
          this.btntxt="获取验证码";
          this.disabled=false;
        }
      },
      sendEmailMsg(){
        this.emailTime=60
        this.isdisabled=true
        this.Emailtimer()
        axios.get(`http://localhost/buyerSendSmsVcode?phoneNum=${this.form.phone}`,{
          xhrFields :
            {
              withCredentials: true
            }
        }).then(

          res=>{

            console.log(res.data)
          })

      },
      Emailtimer() {
        if (this.emailTime > 0) {
          this.emailTime--;
          this.btnname=this.emailTime+"s后重新获取";
          setTimeout(this.Emailtimer, 1000);
        } else{
          this.emailTime=0;
          this.btnname="获取验证码";
          this.isdisabled=false;
        }
      },
      submitForm (formName) {
        this.$refs[formName].validate(valid => {
          if (valid) {
            this.$router.push('/resetpwd')
          }
          else {
            console.log('error submit!!')
            return false
          }
        })
      },
      submitFromEmail (formName) {
        this.$refs[formName].validate(valid => {
          if (valid) {
            this.$router.push('/resetpwd')
          }
          else {
            console.log('error submit!!')
            return false
          }
        })
      }
    }

  }


</script>
