<template>
  <div class="">
    <div class="header">
      <div class="top">
        <img src="./logo.png" alt="">
        <span>创展世贸电商生态平台</span>
      </div>
    </div>
    <div class="middle">
      <div class="hello" >
            <h2>重置密码</h2>
            <el-form ref="form" class="el-form" :rules="rules" :model="form" label-width="100px">
              <el-form-item label="设置密码" prop="setPwd" >
                <el-input class="inp"  v-model='form.setPwd' auto-complete="true"></el-input>
              </el-form-item>
              <el-form-item label="确认密码" prop="cPwd" >
                <el-input class="inp"  v-model='form.cPwd' auto-complete="true"></el-input>
              </el-form-item>
              <el-button class="btn-login check order" type="primary"  size="medium" @click="submitResetPwd('form')">提交</el-button>
              <h5><span>记住密码可以在此点击</span><router-link to="/login">登陆</router-link></h5>
            </el-form>
      </div>
    </div>
    <div class="footer">
      <p><a href="">首页</a>|<a href="">隐私保护</a>|<a href="">联系我们</a>|<a href="">免费条款</a>|<a href="">商家入驻</a>|<a href="">意见反馈</a></p>
      <p class="message">©2009-2018深圳市创展世贸国际展览有限公司 粤ICP备16056256号-1</p>
    </div>
  </div>


</template>
<script>


  import axios from 'axios'
  export default {
    data() {
      const pPattern = /^.*(?=.{4,})(?=.*\d)(?=.*[!@#$%^&*? ]).*$/;
      const validatesetPwd = (rule, value, callback) => {
        if (!value) {
          return callback(new Error('请填写密码!'))
        }
        setTimeout(() => {
          if (!pPattern.test(value)) {
            callback(new Error('最少4位，包括1个数字，1个特殊字符'))
          } else {
            this.form.setPwd=value
            const url = `http://localhost/buyerAccountTelExise?uTel=${this.form.setPwd}`
            axios.get(url).then(response => {
              if(response.data.msg=='canRegister'){

                callback()
              }
              console.log(response.data)
              callback(new Error('手机号已经注册!'))
            })
          }
        }, 10)
      }
      const validateccPwd=(rule,value,callback)=>{
        if (value === '') {
          callback(new Error('请再次输入密码'))
        } else if (value !== this.form.setPwd) {
          callback(new Error('两次输入密码不一致!'))
        } else {
          this.form.cPwd=value
          callback()
        }
      }
      return {
        form: {
          setPwd:'',
          cPwd:''
        },
        // 校验规则
        rules: {
          // 校验手机号码，主要通过validator来指定验证器名称
          setPwd: [{ validator: validatesetPwd, trigger: 'blur' }],
          cPwd:[
            {
              validator: validateccPwd, trigger: 'blur'
            }
          ]
        }
      }
    },
    methods: {
      handleClick(tab, event) {
        console.log(tab, event);
      },

      submitResetPwd(formName) {
        this.$refs[formName].validate(valid => {
          if (valid) {

            axios.get(`http://localhost/buyerLogin?username=${this.form.setPwd}`)
            then(response => {
              console.log(response.data)
              if(response.data.msg=='success'){
                callback()
              }
              else{

                callback(new Error('获取数据错误!'))
              }

            })
              .catch(function (error) {
                console.log(error);
              });
          }
          else {
            console.log('error submit!!')
            return false
          }
        })
      }
    }

  }


</script>
<style scoped>
  html, body, div, span{
    margin: 0;
    padding: 0;
  }
  .wrapper{
    margin-top: 34px;
  }
  h1, h2{
    font-weight: normal;
    display: inline-block;
  }
  h1, h2{
    font-weight: normal;
    display: inline-block;
    color:#039BC3;
  }
  .el-tabs--top{
    font-size: 16px;
    margin-top: 8%;
  }
  h5{
    font-weight:normal;
    display: block;
    text-align: center;
    margin-left: 18%;
  }
  .header{
    padding-left: 80px;
  }
  .top{
    display: table-cell;
    text-align: center;
    height: 80px;
    min-width: 510px;
  }
  .sub {
    position: absolute;
    right: 0;
    width: 44%;
  }
  .con{
    margin-left: 27%;
    margin-top: -8px;
  }
  .btn-login{
    border-radius: 8px;
  }
  .first .el-form{
    width: 269px;
  }
  .el-form{
    width: 88%;
    margin-top: 8%;
  }
  .el-form .order {
    width: 80%;
    margin-left: 20%;
    border-radius: 8px;
    margin-bottom: 20px;
  }
  .el-tab-pane{
    width: 100%;
  }
  .el-input__inner {
    border-radius: 8px;
  }
  .el-form-item {
    margin-bottom: 23px;

  }
  .middle{
    position: relative;
  }
  .el-tabs__item{
    padding: 0 15px;
    font-size: 16px;
  }
  .el-tabs__item.is-active,.el-tabs__item:hover{
    color: #1CA2C7;
  }
  .el-tabs__nav-wrap::after,.el-tabs__active-bar{
    height: 0;
  }
  .el-tabs__nav-scroll{
    text-align: center;
    margin-left: 28%!important;
  }
  .el-tabs__header .el-tabs__nav-scroll {
    overflow: hidden;
    margin-left: 28%;
  }
  .el-tabs__nav {
    margin-left: 37%;
  }
  .el-button{
    height: 39px;
  }
  .top img{
    height: 68px;
    vertical-align: middle;
    text-align: center;
  }
  .top span{
    display: inline-block;
    height: 12px;
    font-size: 22px;
    margin-left: 30px;
    color: #4EB0CF;
    font-weight: 500;
    vertical-align: middle;
  }
  .middle{
    background-color:#F9F9F9 ;
    height: 440px;
    padding-top: 8%;
    padding-left: 30%;
  }
  .middle .hello{
    width: 580px;
    height: 388px;
    vertical-align: middle;
    background-color: #FFFFFF;
    border-radius: 8px;
    padding: 8px;
    font-family: "Microsoft Yahei";
    font-weight: 600;
    box-shadow: 0 8px 10px 0 #B5B5B5;
  }
  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #40AAFF;
  }
  .check{
    width: 52%;
    float: left;
    margin-left: 2px;
  }
  .footer p a{
    display: inline-block;
    font-size: 14px;
    text-decoration: none;
    color:black;
    margin-right: 8px;
    margin-left: 8px;
  }
  .footer .message{
    font-size: 12px;
  }
</style>



